  display: none;
  content: "palm"; }
  @media screen and (min-width: 400px) {
    body:before {
      content: "portable-and-up"; } }
  @media screen and (min-width: 684px) {
    body:before {
      content: "lap-and-up"; } }
  @media screen and (min-width: 1000px) {
    body:before {
      content: "desk"; } }
